import { graphql, useStaticQuery } from 'gatsby';

const useArticulos = () => {

    const data = useStaticQuery(graphql`
    query {
        allDatoCmsArticulo {
            nodes {
                titulo
                id
                slug
                  descripcion
                imgdestacada {
                    fluid (maxWidth: 350){
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
    `);

    return data.allDatoCmsArticulo.nodes.map(articulo => ({
        titulo: articulo.titulo,
        id: articulo.id,
        descripcion: articulo.descripcion,
        imgdestacada: articulo.imgdestacada,
        slug: articulo.slug,
    }))
}
 
export default useArticulos;